import { Pencil, Trash2 } from "lucide-react";
import { useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import ConfirmDeleteModal from "../../ConfrimDeleteModal";
import toast from "react-hot-toast";

const ProductTable = ({ onEdit, products }) => {
  const [allProducts, setAllProducts] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [targetItem, setTargetItem] = useState(null);

  const openDeleteModal = (item) => {
    setTargetItem(item);
    setIsModalOpen(true);
  };

  useEffect(() => {
    setAllProducts(products);
  }, [products]);

  const handleDeleteProduct = (id) => {
    let filteredProducts = allProducts.filter((p) => p.id !== id);
    setAllProducts(filteredProducts);

    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/products/"
        : "https://allmaxsport.com/api/products/";

    try {
      fetch(`${API_URL}?id=${id}`, {
        method: "DELETE",
      }).then((res) => {
        toast.success("محصول با موفقیت حذف شد.");
      });
    } catch (error) {}
  };

  return (
    <>
      <Helmet>
        <title>المکس | محصولات</title>
      </Helmet>

      {/* Desktop Table */}
      <div className="overflow-x-auto rounded-xl border dark:border-neutral-700 hidden md:block">
        <table className="min-w-full bg-white dark:bg-neutral-800 text-right">
          <thead className="bg-gray-50 dark:bg-neutral-700 text-gray-700 dark:text-gray-300">
            <tr>
              <th className="p-3 text-sm font-semibold">نام</th>
              <th className="p-3 text-sm font-semibold">دسته</th>
              <th className="p-3 text-sm font-semibold">قیمت</th>
              <th className="p-3 text-sm font-semibold">موجودی</th>
              <th className="p-3 text-sm font-semibold">عملیات</th>
            </tr>
          </thead>
          <tbody className="text-sm text-gray-600 dark:text-gray-200">
            {allProducts.map((product) => (
              <tr key={product.id} className="border-t dark:border-neutral-700">
                <td className="p-3">{product.name}</td>
                <td className="p-3">{product.category}</td>
                <td className="p-3">
                  {Number(product?.price)?.toLocaleString("fa-IR")} تومان
                </td>
                <td className="p-3">
                  {Number(product?.stock)?.toLocaleString("fa-IR")}
                </td>
                <td className="p-3 flex gap-2">
                  <button
                    onClick={() => onEdit(product)}
                    className="text-blue-600 hover:text-blue-800"
                  >
                    <Pencil size={16} />
                  </button>
                  <button
                    onClick={() => openDeleteModal(product)}
                    className="text-red-600 hover:text-red-800"
                  >
                    <Trash2 size={16} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Mobile Cards */}
      <div className="md:hidden space-y-6">
        {allProducts.map((product) => (
          <div
            key={product.id}
            className="bg-white dark:bg-neutral-800 rounded-2xl shadow-lg border border-gray-200 dark:border-neutral-700 overflow-hidden transition-transform hover:-translate-y-1 hover:shadow-xl"
          >
            <div className="p-5 space-y-4">
              {/* Product Name */}
              <h3 className="text-xl font-extrabold text-gray-900 dark:text-white leading-snug border-b border-gray-200 dark:border-neutral-700 pb-3">
                {product.name}
              </h3>

              {/* Category & Stock */}
              <div className="flex justify-between items-center text-sm text-gray-500 dark:text-gray-400">
                <span className="inline-flex items-center gap-1">
                  <span className="text-gray-400 dark:text-gray-500">
                    دسته:
                  </span>
                  <span className="bg-blue-100 dark:bg-blue-900 text-blue-800 dark:text-blue-200 rounded-full px-3 py-0.5 text-xs font-medium">
                    {product.category}
                  </span>
                </span>

                <span className="inline-flex items-center gap-1">
                  <span className="text-gray-400 dark:text-gray-500">
                    موجودی:
                  </span>
                  <span className="font-semibold text-gray-700 dark:text-gray-300">
                    {Number(product?.stock)?.toLocaleString("fa-IR")}
                  </span>
                </span>
              </div>

              {/* Price */}
              <div className="flex justify-between items-center mt-2">
                <span className="text-sm text-gray-500 dark:text-gray-400">
                  قیمت:
                </span>
                <span className="text-lg font-bold text-green-600 dark:text-green-400">
                  {Number(product?.price)?.toLocaleString("fa-IR")} تومان
                </span>
              </div>

              {/* Actions */}
              <div className="flex gap-3 pt-4 border-t border-gray-200 dark:border-neutral-700">
                <button
                  onClick={() => onEdit(product)}
                  className="flex-1 inline-flex justify-center items-center gap-2 px-4 py-2 rounded-xl bg-blue-50 hover:bg-blue-100 text-blue-600 font-medium text-sm transition"
                >
                  <Pencil size={18} />
                  ویرایش
                </button>
                <button
                  onClick={() => openDeleteModal(product)}
                  className="flex-1 inline-flex justify-center items-center gap-2 px-4 py-2 rounded-xl bg-red-50 hover:bg-red-100 text-red-600 font-medium text-sm transition"
                >
                  <Trash2 size={18} />
                  حذف
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>

      <ConfirmDeleteModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onDelete={() => handleDeleteProduct(targetItem?.id)}
        itemLabel={targetItem?.name}
      />
    </>
  );
};

export default ProductTable;
