import { useState } from "react";
import { X } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { toast } from "react-hot-toast";

export default function DiscountCodeModal({ onClose }) {
  const [form, setForm] = useState({
    code: "",
    percentage: "",
    expire_date: "",
  });
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async () => {
    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/orders/"
        : "https://allmaxsport.com/api/orders/";

    const { code, percentage, expire_date } = form;

    // Basic validation
    if (!code || !percentage || !expire_date) {
      toast.error("تمام فیلدها الزامی هستند");
      return;
    }
    if (isNaN(percentage) || percentage < 1 || percentage > 100) {
      toast.error("درصد باید بین ۱ تا ۱۰۰ باشد");
      return;
    }

    try {
      setLoading(true);
      const response = await fetch(
        `${API_URL}?discount_code=true`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            code,
            percentage,
            expire_date,
          }),
        }
      );

      if (!response.ok) {
        throw new Error("خطا در ایجاد کد تخفیف");
      }

      toast.success("کد تخفیف با موفقیت ایجاد شد");
      onClose();
    } catch (error) {
      toast.error("ایجاد کد تخفیف با خطا مواجه شد");
    } finally {
      setLoading(false);
    }
  };

  return (
    <AnimatePresence>
      <motion.div
        key="backdrop"
        className="fixed inset-0 bg-black/50 flex items-center justify-center z-50"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <motion.div
          key="modal"
          initial={{ scale: 0.9, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          exit={{ scale: 0.95, opacity: 0 }}
          transition={{ duration: 0.2 }}
          className="bg-zinc-900 text-white p-6 rounded-2xl w-full max-w-md shadow-lg relative"
        >
          <button
            onClick={onClose}
            className="absolute top-3 right-3 text-zinc-400 hover:text-white"
          >
            <X size={20} />
          </button>

          <h2 className="text-lg font-semibold mb-5">افزودن کد تخفیف جدید</h2>

          <div className="space-y-4">
            <div>
              <label className="block text-sm mb-1">کد تخفیف</label>
              <input
                type="text"
                name="code"
                value={form.code}
                onChange={handleChange}
                placeholder="مثلاً: OFF50"
                className="w-full px-3 py-2 rounded-xl bg-zinc-800 text-white border border-zinc-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>

            <div>
              <label className="block text-sm mb-1">درصد تخفیف</label>
              <input
                type="number"
                name="percentage"
                value={form.percentage}
                onChange={handleChange}
                placeholder="مثلاً: 20"
                min="1"
                max="100"
                className="w-full px-3 py-2 rounded-xl bg-zinc-800 text-white border border-zinc-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>

            <div>
              <label className="block text-sm mb-1">تاریخ انقضا</label>
              <input
                type="date"
                name="expire_date"
                value={form.expire_date}
                onChange={handleChange}
                className="w-full px-3 py-2 rounded-xl bg-zinc-800 text-white border border-zinc-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>

            <div className="flex justify-end gap-3 pt-4">
              <button
                onClick={onClose}
                className="px-4 py-2 text-sm bg-zinc-700 hover:bg-zinc-600 rounded-xl"
                disabled={loading}
              >
                انصراف
              </button>
              <button
                onClick={handleSubmit}
                className={`px-4 py-2 text-sm bg-blue-600 hover:bg-blue-700 rounded-xl text-white ${
                  loading ? "opacity-50 cursor-not-allowed" : ""
                }`}
                disabled={loading}
              >
                {loading ? "در حال ذخیره..." : "ذخیره"}
              </button>
            </div>
          </div>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );
}
