import React from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Trash2, X } from "lucide-react";

const ConfirmDeleteModal = ({
  isOpen,
  onClose,
  onDelete,
  itemLabel = "آیتم",
}) => {
  return (
    <AnimatePresence>
      {isOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm px-4">
          <motion.div
            initial={{ opacity: 0, scale: 0.95, y: 20 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            exit={{ opacity: 0, scale: 0.95, y: 20 }}
            transition={{ duration: 0.2, ease: "easeInOut" }}
            className="bg-white dark:bg-neutral-900 w-full max-w-md rounded-2xl shadow-2xl p-6 relative"
          >
            <button
              onClick={onClose}
              className="absolute top-4 left-4 text-gray-400 hover:text-gray-600 dark:hover:text-gray-200"
            >
              <X size={20} />
            </button>

            <div className="text-center">
              <div className="flex justify-center mb-4 text-red-600">
                <Trash2 size={32} />
              </div>
              <h3 className="text-lg font-bold text-gray-800 dark:text-white mb-2">
                حذف {itemLabel}
              </h3>
              <p className="text-sm text-gray-600 dark:text-gray-300">
                آیا از حذف {itemLabel} مطمئن هستید؟ این عملیات قابل بازگشت نیست.
              </p>
            </div>

            <div className="mt-6 flex justify-between items-center gap-3">
              <button
                onClick={onClose}
                className="flex-1 py-2 rounded-xl border border-gray-300 dark:border-neutral-700 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-neutral-800 transition"
              >
                انصراف
              </button>
              <button
                onClick={() => {
                  onDelete();
                  onClose();
                }}
                className="flex-1 py-2 rounded-xl bg-red-600 text-white text-sm hover:bg-red-700 transition"
              >
                تایید حذف
              </button>
            </div>
          </motion.div>
        </div>
      )}
    </AnimatePresence>
  );
};

export default ConfirmDeleteModal;
