import React, { useState, useRef, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { X, UploadCloud, Images, Trash2, ImageIcon, Plus } from "lucide-react";
import toast from "react-hot-toast";
import { useNavigate } from "react-router-dom";

const categories = {
  هوازی: ["تردمیل", "دوچرخه ثابت", "اسکی فضایی", "الپتیکال", "پله"],
  بدنسازی: ["دمبل", "هالتر", "نیمکت", "کراس اور"],
  "لوازم-جانبی": ["موتور", "تسمه تردمیل", "مانیتور"],
  "وزن-آزاد": ["اسمیت", "پرس", "ساق"],
};

const ProductFormModal = ({ onClose, product, onSaved }) => {
  const [form, setForm] = useState({
    name: "", //
    price: "",
    stock: "",
    category: "", //
    type: "", //
    dimensions: "", //
    description: "", //
    brand: "", //
    material: "", //
    weight_capacity: "", //
    display: "", //
    motor_power: "", //
    product_weight: "", //
    features: [], //
    warranty: "", //
    status: "active", //
    sales: 0, //
    images: [], //
  });
  const navigate = useNavigate();
  const [featureInput, setFeatureInput] = useState("");
  const [imagePreviews, setImagePreviews] = useState([]);
  const fileInputRef = useRef(null);
  const dropZoneRef = useRef(null);
  const isEdit = Boolean(product?.id);

  useEffect(() => {
    if (product) {
      let image = product.images;
      setForm({
        name: product.name || "",
        price: product.price || "",
        stock: product.stock || "",
        brand: product.brand || "",
        category: product.category || "",
        type: product.product_type || "",
        dimensions: product.dimensions || "",
        material: product.material || "",
        weight_capacity: product.weight_capacity || "",
        product_weight: product.product_weight || 0,
        weight: product.weight || "",
        display: product.display || "",
        motor_power: product.motor_power || "",
        description: product.description || "",
        status: product.status === "active" ? "فعال" : "غیرفعال",
        sales: product.sales || 0,
        features: product.features || [],
        images: image || [],
        warranty: product.warranty || "",
      });

      setImagePreviews(image || []);
    }
  }, [product]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
    if (name === "category") {
      setForm((prev) => ({ ...prev, type: "" }));
    }
  };

  const handleImageUpload = (files) => {
    // Check if adding these files would exceed the limit
    if (form.images.length + files.length > 4) {
      toast.error("حداکثر تعداد تصاویر برای محصول ۴ است.");
      // Only take as many files as would fill the limit
      const availableSlots = 4 - form.images.length;
      if (availableSlots <= 0) return; // No slots available
      files = files.slice(0, availableSlots);
    }

    const newImages = [...form.images, ...files];
    const previews = newImages.map((file) =>
      typeof file === "string" ? file : URL.createObjectURL(file)
    );
    setForm((prev) => ({ ...prev, images: newImages }));
    setImagePreviews(previews);
  };

  const handleImageDrop = (e) => {
    e.preventDefault();
    const files = Array.from(e.dataTransfer.files);
    handleImageUpload(files);
    dropZoneRef.current.classList.remove("border-blue-500");
  };

  const handleImageSelect = (e) => {
    const files = Array.from(e.target.files);

    setForm((prev) => {
      const existingFiles = prev.images || [];

      // Filter out files that already exist (based on name + size)
      const newUniqueFiles = files.filter((file) => {
        return !existingFiles.some(
          (existing) =>
            existing.name === file.name && existing.size === file.size
        );
      });

      // Check if adding these files would exceed the limit
      if (existingFiles.length + newUniqueFiles.length > 4) {
        toast.error("Maximum 4 images allowed per product");
        // Only take as many files as would fill the limit
        const availableSlots = 4 - existingFiles.length;
        if (availableSlots <= 0) return prev; // No slots available
        const limitedNewFiles = newUniqueFiles.slice(0, availableSlots);

        // Update image previews with the limited set
        const updatedImages = [...existingFiles, ...limitedNewFiles];
        const previews = updatedImages.map((file) =>
          typeof file === "string" ? file : URL.createObjectURL(file)
        );
        setImagePreviews(previews);

        return {
          ...prev,
          images: updatedImages,
        };
      }

      // Trigger the upload only with unique new files
      if (newUniqueFiles.length > 0) {
        handleImageUpload(newUniqueFiles);
      }

      return {
        ...prev,
        images: [...existingFiles, ...newUniqueFiles],
      };
    });
  };

  const removeImage = (index) => {
    const newImages = [...form.images];
    newImages.splice(index, 1);
    const newPreviews = [...imagePreviews];
    newPreviews.splice(index, 1);
    setForm((prev) => ({ ...prev, images: newImages }));
    setImagePreviews(newPreviews);
  };

  const handleFeatureAdd = (e) => {
    e.preventDefault();
    const trimmed = featureInput.trim();
    if (trimmed && !form.features.includes(trimmed)) {
      setForm((prev) => ({ ...prev, features: [...prev.features, trimmed] }));
      setFeatureInput("");
    }
  };

  const removeFeature = (index) => {
    const newFeatures = [...form.features];
    newFeatures.splice(index, 1);
    setForm((prev) => ({ ...prev, features: newFeatures }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const IMAGE_API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/upload-images/"
        : "https://allmaxsport.com/api/upload-images/";

    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/products/"
        : "https://allmaxsport.com/api/products/";

    try {
      // 1. Upload new images
      const imageForm = new FormData();
      const newFiles = form.images.filter((img) => img instanceof File);
      const existingUrls = form.images.filter((img) => typeof img === "string");

      newFiles.forEach((file) => imageForm.append("image", file));

      let imageUrls = [];
      if (newFiles.length > 0) {
        const res = await fetch(IMAGE_API_URL, {
          method: "POST",
          body: imageForm,
        });
        const data = await res.json();
        imageUrls = data.image_urls;
      }

      // 2. Combine old & new images
      const allImages = [...existingUrls, ...imageUrls];

      // 3. Build product data
      const productData = {
        name: form.name,
        price: form.price,
        stock: form.stock,
        brand: form.brand,
        category: form.category,
        product_type: form.type,
        dimensions: form.dimensions,
        material: form.material,
        weight_capacity: form.weight_capacity,
        weight: form.product_weight,
        display: form.display,
        motor_power: form.motor_power,
        product_weight: form.product_weight,
        description: form.description,
        status: form.status === "فعال" ? "active" : "inactive",
        sales: form.sales,
        features: form.features,
        images: allImages,
        warranty: form.warranty,
      };
      // 4. Decide if create or edit
      const isEdit = Boolean(product?.id);
      const url = isEdit ? `${API_URL}?id=${product.id}` : `${API_URL}`;

      const method = isEdit ? "PATCH" : "POST";

      const cleanedData = Object.fromEntries(
        Object.entries(productData).filter(
          ([_, v]) => v !== undefined && v !== null
        )
      );

      const response = await fetch(url, {
        method,
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(productData ? productData : cleanedData),
      });

      if (!response.ok) throw new Error("خطا در ذخیره اطلاعات");
      const resData = await response.json();
      onSaved();
      setForm(resData);
      toast.success(isEdit ? "محصول ویرایش شد" : "محصول با موفقیت ثبت شد");
      onClose();
    } catch (err) {
      toast.error("خطا در ذخیره محصول");
    } finally {
      navigate("/dashboard/products");
    }
  };

  return (
    <AnimatePresence>
      <motion.div
        className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center p-4"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <motion.div
          className="bg-white dark:bg-neutral-800 rounded-2xl shadow-xl w-full max-w-3xl relative max-h-[90vh] overflow-y-auto"
          initial={{ y: 50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          exit={{ y: 50, opacity: 0 }}
          transition={{ type: "spring", stiffness: 300, damping: 30 }}
        >
          <button
            onClick={onClose}
            className="absolute left-4 top-4 text-gray-500 hover:text-gray-700 dark:text-gray-300"
          >
            <X size={24} />
          </button>

          <div className="p-6 font-vazir">
            <h2 className="text-2xl font-bold mb-6 text-right text-gray-800 dark:text-white">
              {product ? "ویرایش محصول" : "ثبت محصول جدید"}
            </h2>

            <form onSubmit={handleSubmit}>
              <div className="grid md:grid-cols-2 gap-6">
                {[
                  { label: "نام محصول *", name: "name", required: true },
                  {
                    label: "قیمت (تومان) *",
                    name: "price",
                    type: "number",
                    required: true,
                  },
                  { label: "موجودی", name: "stock", type: "number" },
                  {
                    label: "دسته‌بندی *",
                    name: "category",
                    type: "select",
                    options: Object.keys(categories),
                    required: true,
                  },
                  form.category && {
                    label: "نوع محصول *",
                    name: "type",
                    type: "select",
                    options: categories[form.category],
                    required: true,
                  },
                  { label: "برند *", name: "brand", required: true },
                  { label: "جنس", name: "material" },
                  {
                    label: "تحمل وزن (کیلوگرم)",
                    name: "weight_capacity",
                    type: "number",
                  },
                  { label: "صفحه نمایش", name: "display" },
                  { label: "قدرت موتور", name: "motor_power" },
                  {
                    label: "وزن محصول (کیلوگرم)",
                    name: "product_weight",
                    type: "number",
                  },
                  { label: "ابعاد", name: "dimensions" },
                  { label: "گارانتی", name: "warranty" },
                  {
                    label: "وضعیت *",
                    name: "status",
                    type: "select",
                    options: ["فعال", "غیرفعال"],
                    required: true,
                  },
                ]
                  .filter(Boolean)
                  .map(
                    ({
                      label,
                      name,
                      type = "text",
                      options = [],
                      required,
                    }) => (
                      <div key={name}>
                        <label className="block text-sm font-medium mb-1 text-gray-600 dark:text-gray-300">
                          {label}
                        </label>
                        {type === "select" ? (
                          <select
                            name={name}
                            value={form[name] || ""}
                            onChange={handleChange}
                            required={required}
                            className="w-full border p-2 rounded-xl text-sm dark:bg-neutral-700 dark:border-neutral-600 text-white"
                          >
                            <option value="">انتخاب کنید</option>
                            {options.map((opt) => (
                              <option key={opt} value={opt}>
                                {opt}
                              </option>
                            ))}
                          </select>
                        ) : (
                          <input
                            name={name}
                            value={form[name] || ""}
                            type={type}
                            onChange={handleChange}
                            required={required}
                            className="w-full border p-2 rounded-xl text-sm bg-white dark:bg-neutral-700 dark:border-neutral-600 text-white"
                          />
                        )}
                      </div>
                    )
                  )}

                <div className="md:col-span-2">
                  <label className="block text-sm font-medium mb-1 text-gray-600 dark:text-gray-300">
                    ویژگی‌ها
                  </label>
                  <div className="flex items-center gap-2 mt-3">
                    <input
                      value={featureInput}
                      onChange={(e) => setFeatureInput(e.target.value)}
                      className="flex-1 border p-2 rounded-xl text-sm bg-white dark:bg-neutral-700 dark:border-neutral-600 text-gray-900 dark:text-white placeholder:text-gray-400 dark:placeholder:text-gray-500"
                      placeholder="ویژگی را بنویسید و Enter یا + بزنید"
                      onKeyDown={(e) =>
                        e.key === "Enter" && handleFeatureAdd(e)
                      }
                      type="text"
                      inputMode="text"
                    />
                    <button
                      onClick={handleFeatureAdd}
                      className="px-3 py-2 rounded-xl bg-blue-600 text-white text-sm font-semibold hover:bg-blue-700 transition-all duration-200 flex items-center justify-center"
                      aria-label="افزودن ویژگی"
                    >
                      +
                    </button>
                  </div>

                  <div className="flex flex-wrap mt-3 gap-2">
                    {form?.features?.map((feat, index) => (
                      <span
                        key={index}
                        className="bg-blue-600 text-white px-3 py-1 rounded-full text-sm flex items-center gap-2"
                      >
                        {feat}
                        <button
                          type="button"
                          onClick={() => removeFeature(index)}
                          className="text-white hover:text-gray-200"
                        >
                          &times;
                        </button>
                      </span>
                    ))}
                  </div>
                </div>

                <div className="md:col-span-2">
                  <label className="block text-sm font-medium mb-1 text-gray-600 dark:text-gray-300">
                    توضیحات
                  </label>
                  <textarea
                    name="description"
                    value={form?.description}
                    onChange={handleChange}
                    rows={4}
                    className="w-full border p-2 rounded-xl text-sm bg-white dark:bg-neutral-700 dark:border-neutral-600 text-white resize-none"
                  />
                </div>

                <div className="md:col-span-2">
                  <ProductImagesContainer
                    form={form}
                    dropZoneRef={dropZoneRef}
                    handleImageDrop={handleImageDrop}
                    fileInputRef={fileInputRef}
                    handleImageSelect={handleImageSelect}
                    imagePreviews={imagePreviews}
                    removeImage={removeImage}
                  />
                </div>
              </div>

              <button
                type="submit"
                className="w-full mt-6 bg-blue-600 text-white py-2 rounded-xl hover:bg-blue-700 transition text-sm"
              >
                {product ? "ذخیره تغییرات" : "ثبت محصول"}
              </button>
            </form>
          </div>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );
};

export default ProductFormModal;

const ProductImagesContainer = ({
  form,
  dropZoneRef,
  handleImageDrop,
  fileInputRef,
  handleImageSelect,
  imagePreviews,
  removeImage,
}) => {
  return (
    <div className="mt-4">
      <label className="block text-sm font-medium text-gray-300 mb-2">
        تصاویر محصول {form.images.length > 0 && `(${form.images.length}/4)`}
      </label>

      {/* Drop zone */}
      <div
        ref={dropZoneRef}
        onDragOver={(e) => {
          e.preventDefault();
          dropZoneRef.current.classList.add("border-blue-500");
        }}
        onDragLeave={(e) => {
          e.preventDefault();
          dropZoneRef.current.classList.remove("border-blue-500");
        }}
        onDrop={handleImageDrop}
        className={`border-2 border-dashed rounded-lg p-4 text-center cursor-pointer transition-colors
          ${
            form.images.length >= 4
              ? "bg-gray-100 border-gray-300"
              : "hover:border-blue-400 border-gray-300"
          }`}
        onClick={() => form.images.length < 4 && fileInputRef.current.click()}
      >
        <input
          type="file"
          ref={fileInputRef}
          onChange={handleImageSelect}
          multiple
          accept="image/*"
          className="hidden"
          disabled={form.images.length >= 4}
        />

        {form.images.length >= 4 ? (
          <p className="text-gray-500">حداکثر ۴ تصویر انتخاب شده است</p>
        ) : (
          <>
            <div className="flex justify-center mb-2">
              {form.images.length === 0 ? (
                <ImageIcon className="w-8 h-8 text-gray-400" />
              ) : (
                <Plus className="w-8 h-8 text-gray-400" />
              )}
            </div>
            <p className="text-sm text-gray-500 font-vazir">
              تصاویر را اینجا رها کنید یا{" "}
              <span className="text-blue-500">انتخاب کنید</span>
            </p>
            <p className="text-xs text-gray-400 mt-1 font-vazir">
              {4 - form.images.length} جای خالی باقی مانده است
            </p>
          </>
        )}
      </div>

      {/* Image previews grid */}
      {imagePreviews.length > 0 && (
        <div className="grid grid-cols-2 sm:grid-cols-4 gap-4 mt-4">
          <AnimatePresence>
            {imagePreviews.map((preview, index) => (
              <motion.div
                key={preview}
                initial={{ opacity: 0, scale: 0.8 }}
                animate={{ opacity: 1, scale: 1 }}
                exit={{ opacity: 0, scale: 0.5, transition: { duration: 0.2 } }}
                transition={{ type: "spring", stiffness: 300, damping: 24 }}
                className="relative group overflow-hidden rounded-lg border border-gray-200 shadow-sm"
              >
                <div className="aspect-square relative overflow-hidden">
                  <img
                    src={preview}
                    alt={`تصویر محصول ${index + 1}`}
                    className="object-cover w-full h-full transition-transform group-hover:scale-105"
                  />
                </div>

                {/* Stylish remove button with overlay effect */}
                <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-40 transition-all duration-300 flex items-center justify-center">
                  <motion.button
                    onClick={(e) => {
                      e.preventDefault(); // Prevent any form submission
                      removeImage(index);
                    }}
                    type="button" // Explicitly set button type to avoid form submission
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: 0.9 }}
                    className="opacity-0 group-hover:opacity-100 transition-opacity duration-300 bg-red-500 hover:bg-red-600 rounded-full p-2 text-white shadow-lg"
                    aria-label="حذف تصویر"
                  >
                    <Trash2 className="w-4 h-4" />
                  </motion.button>
                </div>

                {/* Image number badge */}
                <div className="absolute top-2 left-2 bg-black bg-opacity-60 text-white text-xs px-2 py-1 rounded-full">
                  {index + 1}/4
                </div>
              </motion.div>
            ))}
          </AnimatePresence>
        </div>
      )}
    </div>
  );
};
